.slots.spinning .outer-col:nth-of-type(2) .col {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}

.slots.spinning .outer-col:nth-of-type(3) .col  {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

.slots.spinning .outer-col:nth-of-type(4) .col {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

.slots.spinning .outer-col:nth-of-type(5) .col {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

.slots.spinning .outer-col:nth-of-type(6) .col {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}

.slots.spinning .col {
  -webkit-animation: scroll 3s cubic-bezier(.7,0,.7,1) 1, blur 3s cubic-bezier(.7,0,.7,1) 1;
          animation: scroll 3s cubic-bezier(.7,0,.7,1) 1, blur 3s cubic-bezier(.7,0,.7,1) 1;
  -webkit-animation-direction: reverse;
          animation-direction: reverse;
}

@-webkit-keyframes scroll {
  0% {-webkit-transform: translate3d(0,0,0);transform: translate3d(0,0,0);}
  10% {-webkit-transform: translate3d(0,10px,0);transform: translate3d(0,10px,0);}
  /*90% {transform: translate3d(0,calc(-100% + 482px),0);}*/
  100% {-webkit-transform: translate3d(0,calc(-100% + 482px),0);transform: translate3d(0,calc(-100% + 482px),0);}
}

@keyframes scroll {
  0% {-webkit-transform: translate3d(0,0,0);transform: translate3d(0,0,0);}
  10% {-webkit-transform: translate3d(0,10px,0);transform: translate3d(0,10px,0);}
  /*90% {transform: translate3d(0,calc(-100% + 482px),0);}*/
  100% {-webkit-transform: translate3d(0,calc(-100% + 482px),0);transform: translate3d(0,calc(-100% + 482px),0);}
}

@-webkit-keyframes blur {
  0% {-webkit-filter: blur(0);filter: blur(0);}
  10% {-webkit-filter: blur(0);filter: blur(0);}
  0% {-webkit-filter: blur(0);filter: blur(0);}
  30% {-webkit-filter: blur(0);filter: blur(0);}
  40% {-webkit-filter: blur(4px);filter: blur(4px);}
  50% {-webkit-filter: blur(6px);filter: blur(6px);}
  80% {-webkit-filter: blur(0);filter: blur(0);}
}

@keyframes blur {
  0% {-webkit-filter: blur(0);filter: blur(0);}
  10% {-webkit-filter: blur(0);filter: blur(0);}
  0% {-webkit-filter: blur(0);filter: blur(0);}
  30% {-webkit-filter: blur(0);filter: blur(0);}
  40% {-webkit-filter: blur(4px);filter: blur(4px);}
  50% {-webkit-filter: blur(6px);filter: blur(6px);}
  80% {-webkit-filter: blur(0);filter: blur(0);}
}

@-webkit-keyframes wiggle {
  0% {-webkit-transform: rotate(10deg);transform: rotate(10deg);}
  50% {-webkit-transform: rotate(-10deg);transform: rotate(-10deg);}
  100% {-webkit-transform: rotate(10deg);transform: rotate(10deg);}
}

@keyframes wiggle {
  0% {-webkit-transform: rotate(10deg);transform: rotate(10deg);}
  50% {-webkit-transform: rotate(-10deg);transform: rotate(-10deg);}
  100% {-webkit-transform: rotate(10deg);transform: rotate(10deg);}
}


